import React, {useEffect, useRef} from "react";
import styled from "styled-components";
import {connect} from "react-redux";
import Nav from "../Components/Nav";
import Footer from "../Components/Footer";
import { useNavigate } from "react-router";
import { auth, signInWithGoogle, logout } from "../Firebase/firebase";
import Google from '../Images/Icons/Google.png';
import { Content } from '../Styles/global';
import {gql, useMutation, useQuery} from "@apollo/client";
import {store} from "../store/TripUsStore";

function CreateGroup(props) {
    const history = useNavigate();

    function handleRoute(route) {
        // window.location.reload(false);
        history(route);
    }

    let groupName = useRef(null);
    let groupBio = useRef(null);
    let groupImage = useRef(null);
    const [groupNameInput, setGroupNameInput] = React.useState(null);
    const [groupBioInput, setGroupBioInput] = React.useState(null);
    const [groupImageInput, setGroupImageInput] = React.useState(null); //image
    const [clicked, setClicked] = React.useState(false)

    const getBase64 = file => {
        return new Promise(resolve => {
          let baseURL = "";
          // Make new FileReader
          let reader = new FileReader();
    
          // Convert the file to base64 text
          reader.readAsDataURL(file);
    
          // on reader load somthing...
          reader.onload = () => {
            // Make a fileInfo Object
            baseURL = reader.result;
            resolve(baseURL);
          };
        });
      };

    const onChange = (file) => {

        if(!file || file === 'data:') {
            setGroupImageInput(null);
            return;
        }

        getBase64(file)
            .then(base64 => {
                setGroupImageInput(base64);
            })

    }

    const handleClick = (e) => {
        if (groupName.current.value === "") {
            setGroupNameInput(null)
        } else {
            setGroupNameInput(groupName.current.value);
        }

        if (groupBio.current.value === "") {
            setGroupBioInput(null);
        } else {
            setGroupBioInput(groupBio.current.value);
        }

        if (groupImageInput === null) {
            setGroupImageInput('data:image/jpeg;base64,')
        }

        setClicked(true)
    }

    const CheckBlank = () => {
        return <e>Please enter a group name!</e>
    }

    const INSERT_GROUP = gql`mutation insert_Group_one ($object: Group_insert_input!){
        insert_Group_one(object: $object){
            Group_ID
            Group_Name
            Group_Image
            Group_Image
            Leader_ID
            Founding_Date
            Join_Code
        }
    }`

    const ADD_MEMBERSHIP = gql`mutation insert_membership_one ($member: membership_insert_input!) {
        insert_membership_one(object: $member){
            User_ID
            Group_ID
        }
    }`

    const SendData = () => {
        // console.log(birthdayInput)
        // console.log(profileImageInput)

        const userObj = {
            // "Group_ID": globalData.Group_ID,
            "Founding_Date": new Date(),
            "Group_Name": groupNameInput,
            "Group_Bio": groupBioInput,
            "Group_Image": groupImageInput,
            "Leader_ID": sessionStorage.getItem("userId"),
            "Join_Code": joinCode
        }

        const [insert_user, {data,loading,error}] = useMutation(INSERT_GROUP);

        useEffect(() => {

            insert_user({
                variables: {object: userObj}
            });

        }, []);

        if (loading)
            return <p style={{color: "green"}}>Almost done...</p>

        if (data) {
            //console.log(data)
            store.dispatch({type: 'GROUP_CREATION', payload: data.insert_Group_one})
            globalData = data.insert_Group_one
            return <AddMembership/>
        }
        return null

    }

    let globalData = null

    const AddMembership = () => {
        const userObj = {
            "User_ID": sessionStorage.getItem("userId"),
            "Group_ID": globalData.Group_ID
        }

        const [add_membership, {data,loading,error}] = useMutation(ADD_MEMBERSHIP);

        useEffect(() => {

            add_membership({
                variables: {member: userObj}
            });

        }, []);

        if (loading)
            return <p style={{color: "green"}}>Almost done...</p>
        
        window.location.reload(false);    
        handleRoute('/AddMembers')

        return null
    }

    const VALIDATE_ACC = gql`query validateAcc ($Username: String!){
        Group(where: {Join_Code: {_eq: $Username}}) {
            Group_ID
        }
    }`

    let joinCode
    const GenerateCode = () => {
        let characters = '0123456789bcdfghjklmnpqrstvwxyzBCDFGHJKLMNPQRSTVWXYZ'
        joinCode = ''
        let length = 6 // Customize the length here.
        for (let i = length; i > 0; --i) joinCode += characters[Math.round(Math.random() * (characters.length - 1))]

        return <CheckData/>
    }

    const CheckData = () => {
        // console.log(joinCode);
        const {data, error, loading} = useQuery(VALIDATE_ACC, {
            variables : {Username : joinCode}
        });

        if (loading)
            return <p style={{color: "green"}}>Creating group...</p>

        if (data.Group[0]){
            return <GenerateCode/>
        }

        return <SendData/>
    }

    const handleKeypress = e => {
        //it triggers by pressing the enter key
        e.preventDefault();
        console.log(e)
        if (e.type === "submit") {
            handleClick();
        }
    };

    return (
        <Container>
            <Nav />
            <Content theme={props.theme}>
                <CreateTripForm>
                    <Title>Create Group</Title>
                    <Form onSubmit={handleKeypress}>
                        <Row theme={props.theme.theme}>
                            {/* <label>Email:</label> */}
                            <label>Group Name * </label>
                            <input ref={groupName} type="text" name="groupname" placeholder="Enter Group Name:"/><br></br>
                        </Row>
                        <Row theme={props.theme.theme}>
                            {/* <label>Email:</label> */}
                            <label>Group Bio </label>
                            <input ref={groupBio} type="text" placeholder="Tell Us About Your Crew:"/>
                        </Row>
                        <Row theme={props.theme.theme} style={{marginTop: '10px'}}>
                            <label>Group Image (.jpg, .jpeg, .png)</label>
                            <input ref={groupImage} type="file" accept=".jpg, .jpeg, .png" onChange={(event) => onChange(event.target.files[0] || null)}/>
                        </Row>
                        <Row>
                            <SubmitButton theme={props.theme.theme} onClick={handleClick}>Create Group</SubmitButton>
                            {groupNameInput !== null ? <GenerateCode/> : clicked && <CheckBlank/>}

                        </Row>
                    </Form>
                </CreateTripForm>
            </Content>
            <Footer />
        </Container>
    )
}

const mapStateToProps = (state) => {
    return state;
}

export default connect(mapStateToProps)(CreateGroup);

