import React, {useEffect, useRef} from "react";
import styled from "styled-components";
import {connect} from "react-redux";
import Nav from "../Components/Nav";
import Footer from "../Components/Footer";
import { useNavigate } from "react-router";
import { auth, signInWithGoogle, logout } from "../Firebase/firebase";
import Google from '../Images/Icons/Google.png';
import { Content } from '../Styles/global';
import {gql, useMutation, useQuery} from "@apollo/client";
import {store} from "../store/TripUsStore";

function CreateTrip(props) {
    const history = useNavigate();

    function handleRoute(route) {
        // window.location.reload(false);
        history(route);
    }

    let tripName = useRef(null);
    let tripBio = useRef(null);
    let tripImage = useRef(null);
    let tripDest = useRef(null);
    let tripDept = useRef(null);
    let startDate = useRef(null);
    let endDate = useRef(null);
    const [tripBioInput, setTripBioInput] = React.useState(null);
    const [tripImageInput, setTripImageInput] = React.useState(null); //image
    const [startDateInput, setStartDateInput] = React.useState(null);  //date
    const [endDateInput, setEndDateInput] = React.useState(null);  //date
    const [tripNameInput, setTripNameInput] = React.useState(null);
    const [tripDestInput, setTripDestInput] = React.useState(null);
    const [tripDeptInput, setTripDeptInput] = React.useState(null);
    const [clicked, setClicked] = React.useState(false)

    const getBase64 = file => {
        return new Promise(resolve => {
          let baseURL = "";
          // Make new FileReader
          let reader = new FileReader();
    
          // Convert the file to base64 text
          reader.readAsDataURL(file);
    
          // on reader load somthing...
          reader.onload = () => {
            // Make a fileInfo Object
            baseURL = reader.result;
            resolve(baseURL);
          };
        });
      };

    const onChange = (file) => {

        if(!file || file === 'data:') {
            setTripImageInput(null);
            return;
        }

        getBase64(file)
            .then(base64 => {
                setTripImageInput(base64);
            })

    }

    const handleClick = (e) => {
        if (tripName.current.value === "") {
            setTripNameInput(null)
        } else {
            setTripNameInput(tripName.current.value);
        }

        if (tripBio.current.value === "") {
            setTripBioInput(null);
        } else {
            setTripBioInput(tripBio.current.value);
        }
        
        if(tripImageInput === null){
            setTripImageInput('data:image/jpeg;base64,')
        }
        
        if (tripDest.current.value === "") {
            setTripDestInput(null);
        } else {
            setTripDestInput(tripDest.current.value);
        }
        
        if (tripDept.current.value === "") {
            setTripDeptInput(null);
        } else {
            setTripDeptInput(tripDept.current.value);
        }

        if (startDate.current.value === "") {
            setStartDateInput(null)
        } else {
            setStartDateInput(startDate.current.value);
        }
        if (endDate.current.value === "") {
            setEndDateInput(null)
        } else {
            setEndDateInput(endDate.current.value);
        }

        setClicked(true)

    }
    
    const handleKeypress = e => {
        //it triggers by pressing the enter key
        e.preventDefault();
        console.log(e)
        if (e.type === "submit") {
            handleClick();
        }
    };

    const handleKeyDown = (e) => {
        if (e.key === 'Enter') {
           handleClick();
        }
     }

    const CheckBlank = () => {
        return <e>Fields with * next to them cannot be blank!</e>
    }

    const CheckData = () => {
        // tripNameInput !== null && tripDestInput !== null && tripDeptInput !== null && startDateInput !== null && endDateInput
        let currentDate = new Date()
        if (startDate.current.value > endDate.current.value){
            return <e style={{color: "red"}}> End date cannot be before start date. </e>
        }

        if (startDate.current.value < endDate.current.value || endDate < currentDate){
            return <e style={{color: "red"}}> Dates must be after today. </e>
        }
        
        return <SendData/>

    }

    const INSERT_TRIP = gql`mutation insert_Trip_one ($object: Trip_insert_input!){
        insert_Trip_one(object: $object){
              Trip_ID,
              Trip_Name,
              Trip_Bio,
              Trip_Image,
              Destinations,
              Departures,
              Start_Date,
              End_Date
            }
        }`

    const ADD_TRIP_MEMBERSHIP = gql`mutation insert_Trip_memberships_one ($object: Trip_memberships_insert_input!) {
        insert_Trip_memberships_one(object:$object) {
          Trip_ID
          Group_ID
        }
      }`

    const SendData = () => {
        const userObj = {
            "Trip_Name": tripNameInput,
            "Trip_Bio": tripBioInput,
            "Trip_Image": tripImageInput,
            "Destinations": tripDestInput, // need
            "Departures": tripDeptInput, // need
            "Start_Date": startDateInput, // need
            "End_Date": endDateInput // need
        }

        const [insert_trip, {data,loading,error}] = useMutation(INSERT_TRIP);

        useEffect(() => {

            insert_trip({
                variables: {object: userObj}
            });

        }, []);

        if (loading)
            return <p style={{color: "green"}}>Almost done...</p>

        if (data) {
            // console.log(data)
            store.dispatch({type: 'TRIP_INFO', payload: data.insert_Trip_one})
            globalData = data.insert_Trip_one
            return <AddTripMembership/>
            // handleRoute('')
        }
        return null

    }

    let globalData = null
    let groupInfo = sessionStorage.getItem('groupInfo')
    groupInfo = JSON.parse(groupInfo)
    const AddTripMembership = () => {
        // console.log(groupInfo.Group_ID)
        // console.log(sessionStorage.getItem('groupInfo').Group_ID)
        const userObj = {
            "Trip_ID": globalData.Trip_ID,
            "Group_ID": groupInfo.Group_ID
        }

        const [add_membership, {data,loading,error}] = useMutation(ADD_TRIP_MEMBERSHIP);

        useEffect(() => {

            add_membership({
                variables: {object: userObj}
            });

        }, []);

        if (loading)
            return <p>Almost done...</p>

        
        handleRoute('/tripDetails')  

        return null
    }


    return (
        <Container>
            <Nav />
            <Content theme={props.theme}>
                <CreateTripForm>
                    <Title>Create a Trip!</Title>
                    <Form onSubmit={handleKeypress}>
                        <Row theme={props.theme.theme}>
                            <label>Trip Name *</label>
                            {/* <label>Email:</label> */}
                            <input ref={tripName} onKeyDown={handleKeyDown} type="text" name="tripname" placeholder="Ex: Wackadoodles"/><br></br>
                        </Row>
                        <Row theme={props.theme.theme}>
                            <label>Start Date *</label>
                            <input ref={startDate} onKeyDown={handleKeyDown} type="date" />
                        </Row>
                        <Row theme={props.theme.theme}>
                            <label>End Date *</label>
                            <input ref={endDate} onKeyDown={handleKeyDown} type="date" />
                        </Row>
                        <Row theme={props.theme.theme}>
                            <label>Trip Bio</label>
                            <input ref={tripBio} type="text" placeholder="Ex: Traveling to Berlin"/>
                        </Row>
                        <Row theme={props.theme.theme}>
                            <label>What city are you departing from? *</label>
                            <input ref={tripDept} type="text" placeholder="Ex: Houston"/>
                        </Row>
                        <Row theme={props.theme.theme}>
                            <label>Trip Destination City *</label>
                            <input ref={tripDest} type="text" placeholder="Ex: Berlin"/>
                        </Row>
                        <Row theme={props.theme.theme} style={{marginTop: '10px'}}>
                            <label>Trip Image (.jpg, .jpeg, .png)</label>
                            <input ref={tripImage} type="file" accept=".jpg, .jpeg, .png" onChange={(event) => onChange(event.target.files[0] || null)}/>
                        </Row>
                        <Row>
                            <SubmitButton theme={props.theme.theme} onClick={handleClick}>Submit</SubmitButton>
                            {tripNameInput !== null && tripDestInput !== null && tripDeptInput !== null && startDateInput !== null && endDateInput !== null ? <CheckData/> : clicked && <CheckBlank/>}
                        </Row>

                    </Form>
                </CreateTripForm>
            </Content>
            <Footer />
        </Container>
    )
}

const mapStateToProps = (state) => {
    return state;
}

export default connect(mapStateToProps)(CreateTrip);

